@import url(https://fonts.googleapis.com/css?family=Roboto:400,500,700&display=swap);
.text-area {
  width: 100%;
  margin-bottom: 20px;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}

.text-area textarea {
  width: 80vw;
  height: 30vh;
  padding: 10px;
  border-radius: 8px;
  resize: none;
  font-size: 14px;
  font-family: Roboto, sans-serif;
}

.text-area textarea:focus {
  box-shadow: 0 0 1px 1px #8661c1;
}

.text-area .info-text {
  width: 80vw;
  display: flex;
  justify-content: flex-start;
  margin-bottom: 20px;
}

.actions-area {
  width: 100%;
  display: flex;
  justify-content: center;
  align-items: center;
}

.actions-area .actions {
  width: 80%;
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.actions-area .actions .action {
  width: 20%;
  max-width: 150px;
  height: 50px;
  background: #8661c1;
  color: #fff;
  padding: 0 20px;
  border-radius: 8px;
  cursor: pointer;
  border: 0;
  font-size: 14px;
  font-weight: 500;
  margin-bottom: 20px;
  display: flex;
  justify-content: center;
  align-items: center;
  text-align: center;
  -webkit-transition: opacity 0.2s;
  transition: opacity 0.2s;
}

.actions-area .actions .clear {
  background: #fe5f55;
}

.actions-area .action:hover {
  opacity: 0.8;
}

@media screen and (max-width: 768px) {
  .actions-area .actions {
    flex-wrap: wrap;
  }

  .actions-area .actions .action {
    font-size: 14px;
    line-height: 20px;
    width: 80px;
    font-weight: 600;
  }
}

.header-container {
  width: 100%;
  height: 60px;
  padding: 0 24px;
  box-shadow: 0 1px 10px #ccc;
  margin-bottom: 20px;
  display: flex;
  justify-content: space-between;
  align-items: center;
  flex-wrap: wrap;
}

.header-container .content {
  display: flex;
  flex-direction: column;
  align-items: center;
}

.header-container a {
  -webkit-text-emphasis: none;
          text-emphasis: none;
  -webkit-transition: opacity 0.2s;
  transition: opacity 0.2s;
}

.header-container a:hover {
  opacity: 0.8;
}

.footer {
  width: 100%;
  height: 50px;
  bottom: 0;
  left: 0;
  display: flex;
  justify-content: space-around;
  align-items: center;
}

.footer p {
  font-weight: 500;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}

.footer p + p a {
  display: flex;
  align-items: center;
  cursor: pointer;
  text-decoration: none;
  color: #737380;
  -webkit-transition: opacity 0.2s;
  transition: opacity 0.2s;
}

.footer p .git-hub {
  margin-left: 5px;
}

.footer p + p:hover {
  opacity: 0.8;
}

* {
  margin: 0;
  padding: 0;
  outline: 0;
  box-sizing: border-box;
}

body {
  font: 400 14px Roboto, sans-serif;
  background: #f0f0f5;
  color: #737380;
  -webkit-font-smoothing: antialiased;
}

